<template>
  <div class="location-selector">
      <div class="u-margin-bottom--20">
          <nn-radio-button
            :id="`${_uid}-${locationSource}-manual`"
            name="locationSource"
            value="manual"
            :model="locationSource"
            class="u-margin--none"
            @change="onLocationSourceChanged"
          >
            <div>
              <div class="o-flex">
                  <div class="o-flex__col">
                      <input
                          type="text"
                          :class="inputClass"
                          name="plaats"
                          placeholder="Plaats of postcode"
                          :value="value"
                          @change="onValueChanged"
                          @focus="onFocusChanged"
                      >
                  </div>
                  <div v-if="isInvalid" class="o-flex__col o-flex__col--auto  o-flex__col--icon">
                      <span class="c-icon c-icon--pict-warning u-color--medium-orange u-font-size-m"></span>
                  </div>
              </div>
              <div v-if="isInvalid" class="u-color--medium-orange u-font-bold u-margin-top--5">Vul een plaats of postcode in.</div>
            </div>
          </nn-radio-button>

      </div>
      <div class="u-inline-block u-valign-top">
          <div v-if="enableCurrentPositionSelector">
              <nn-radio-button
                  :id="`${_uid}-${locationSource}-current`"
                  name="locationSource"
                  value="current"
                  :model="locationSource"
                  @change="onLocationSourceChanged"
              >
                Mijn huidige locatie
              </nn-radio-button>

          </div>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    enableCurrentPositionSelector: Boolean,
    isInvalid: Boolean,
    locationSource: String,
    onLocationSourceChanged: Function,
    onValueChanged: Function,
    value: String,
  },
  computed: {
    inputClass () {
      return { 'c-form-text': true, 'c-form-text--error': this.isInvalid === true };
    }
  },
  methods: {
    onFocusChanged () {
      this.onLocationSourceChanged('manual');
    }
  }
}
</script>
