import Vue from 'vue';
import VueComponents from '@nn/vue-components';
import Redirector from './redirector';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';
import { vaAnalytics } from './analytics.js';

Vue.use(VueComponents);

export default class VaRedirector {
  constructor (element, options) {
    this.element = element;
    this.options = options;
    this.app = undefined;
    vaAnalytics('vaZoeker.redirector');
  }

  init () {
    this.app = new Vue({
      el: createVueChildContainer(this.element),
      render: h => h(Redirector, {
        props: {
          redirectUrl: this.options.redirectUrl,
          aanstelling: this.options.aanstelling,
          typeKlant: this.options.typeKlant,
        },
      }),
    });
    return this.app;
  }
}
