export const vaAnalytics = (moduleName) => {
  if(moduleName && window && window.app.analytics && window._al && window.app.analytics.registerTracker){
    window._al.trackers.linkList.skipSelector('[data-module="' + moduleName + '"] .c-link');
    window._al.trackers.externalClicks.skipSelector('[data-module="' + moduleName + '"] a');
    window._al.trackers.moreLinks.attachSelector('[data-module="' + moduleName + '"] .c-link');
    createTracker('form-field-interaction', 'click', 'interaction', 'value', '.c-form-radio');
    createTracker('form-field-interaction', 'change', 'interaction', 'name', '.c-form-text');
    createTracker('more-links', 'change', 'change', 'value', '.c-form-select');
    createTracker('outbound', 'click', 'click', 'href', 'a');
  }
}

const createTracker = (eventCategory, eventType, eventAction, key, selector) => {
  const vaTracker = new window._al.Tracker('element-tracking', eventCategory,{
    type: eventType,
    action() {
      return eventAction;
    },
    label(element) {
      return element[key];
    }
  })

  vaTracker.attachSelector(selector);
  window.app.analytics.registerTracker(vaTracker);
}
