<template>
  <form @submit.prevent="submit">
    <LocationSelector
        :locationSource="locationSource"
        :onLocationSourceChanged="onLocationSourceChanged"
        :value="location"
        :onValueChanged="onLocationSelectorValueChanged"
        :enableCurrentPositionSelector="true"
        :isInvalid="isInvalid"
    />
    <nn-button type="submit">Zoeken</nn-button>
  </form> 
</template>

<script>
  import LocationSelector from './locationSelector';
  import { assign } from '@nn/psdl-utility/src/location';

  export default {
    components: { LocationSelector },
    props: {
      aanstelling: String,
      redirectUrl: String,
      typeKlant: String,
    },
    data: () => ({
      locationSource: 'manual',
      location: '',
      isInvalid: null,
    }),
    methods: {
      onLocationSelectorValueChanged (e) {
        this.location = e.target.value;
      },
      onLocationSourceChanged (e) {
        this.locationSource = e.target.value;
      },
      validate () {
        this.isInvalid = false;
        if (this.locationSource === 'manual' && !this.location) {
          this.isInvalid = true;
        }
      },
      submit () {
        this.validate();
        if (this.isInvalid) {
          return;
        }

        if (this.redirectUrl && this.aanstelling) {
          const location = this.locationSource === 'current' ? 'current' : this.location;
          const typeKlant = this.typeKlant ? `&typeKlant=${this.typeKlant}` : '';
          assign(`${this.redirectUrl}?aanstelling=${this.aanstelling}&location=${location}${typeKlant}`);
        }
      }
    }
  }
</script>
